<template>
    <component :vo="this.vo" :is="component"></component>
</template>

<script>
export default {
  name: 'AnswerLoader',
  data () {
    return {
      component: null
    }
  },
  props: {
    vo: {required: true, default: null}
  },
  computed: {
    loadAnswer () { return () => import(`@/components/survey/response/space/answer/${this.vo.type}.vue`) }
  },
  created () {
    this.component = () => this.loadAnswer()
  }
}
</script>

<style scoped>
</style>



